

.wrapper_XO{
  position: relative;
  top: 20px;
  left: 5%;
  width: 360px;
  height: 360px;
  display: flex;
  flex-wrap: wrap;
}

.XO__square{
  line-height: 100%;
  width: 118px;
  height: 118px;
  text-align: center;
  font-size: 90px;
}
.section__top_right{
 border-left: 2px solid black ;
 border-bottom: 2px solid black;
}
.section__top_mid{
 border-bottom: 2px solid black;
}
.section__top_left{
border-right: 2px solid black;
border-bottom: 2px solid black;
}
.section__mid_right{
  margin-top: -6px;
border-left: 2px solid black;
border-bottom: 2px solid black;
}
.section__mid_mid{
  margin-top: -6px;
border-bottom: 2px solid black;
}
.section__mid_left{
  margin-top: -6px;
border-right: 2px solid black;
border-bottom: 2px solid black;
}
.section__bottom_right{
  margin-top: -6px;
border-left: 2px solid black;
}
.section__bottom_mid{
  line-height: 80%;
  
}
.section__bottom_left{
  margin-top: -6px;
border-right: 2px solid black;
}

.pop_up{
  width: 50%;
  height: 50%;
  position: relative;
  top: 50%;
  left: 50%;
  border-radius: 15px;
  background-color: rgb(233, 233, 205);
  color: brown;
  font-size: 28px;
  font-family: Arial, Helvetica, sans-serif;
}



